import view_video from '@/components/video/watch'
import forumobjct from "@/components/script/forumobjct";
import videoobject from "@/components/script/videoobject";
import comment_reply from "@/components/comment/reply";
import comment_list from "@/components/comment/list";
import view_watch_limit from '@/components/video/watch_limit'
import releated_list from '@/components/video/item/releated_list';
import playerbottom_bannerlist from "../../ads/playerbottom_bannerlist";


import player_plyr from '@/components/video/player/plyr'
import video_tab from "@/components/video/item/video_tab";
import video_jav from "@/components/video/item/video_jav";
import video_servers from "../item/video_servers";

export default {
  name: "video_view",
  mixins: [forumobjct, videoobject],
  components: {
    video_servers,
    video_jav,
    video_tab,
    view_video,
    comment_reply,
    comment_list,
    view_watch_limit,
    releated_list,
    player_plyr,
    playerbottom_bannerlist
  },
  data() {
    return {
      topic_data: undefined,
      forum_conf: undefined,
      edit: false,
      reletad_list: undefined,
      side_threads: undefined,
      select_server_name: undefined,
      online_video: undefined,
      show: true
    }
  },
  computed: {
    is_jav_video: function () {
      var video_info = this.topic_data.video_info;
      return video_info['av_data_type'] === 2;
    },
    is_manager: function () {
      try {
        return this.$store.state.user.userinfo.is_manager
      } catch (e) {

      }
      return false;
    },
    show_view_watch_limit: function () {
      return false;
      var look_list = this.$store.state.user_visit.get_look_list();
      console.log('[show_view_watch_limit]:', look_list);
      if (!this.$store.state.hasLogin && look_list.length > 2) {
        return true;
      }
      if (look_list && look_list.length >= 5 && !this.$store.state.user.userinfo.is_vip) {
        return true;
      }
      return false;
    },
  },
  created() {
    this.load_video();
    this.side_threads = this.get_side_threads();
    this.select_server_name = localStorage.getItem('select_server_name');
    if (!this.select_server_name) {
      this.select_server_name = 'cdn_xxxhostcf';
    }
  },
  methods: {
    init: function () {
      if (this.topic_data && this.topic_data.video_info) {
        this.online_video = this.topic_data.video_info.online_videos[0];
      }
    },
    load_video: function () {
      var cid = this.$route.params.id;
      var urlvars = this.getUrlVars();
      if (urlvars.edit) {
        console.log('处于编辑的帖子')
      }
      this.get_topic({
        id: cid,
        from_cache: this.from_cache(),
        releted: true,
        edit: urlvars.edit,
        cb: this.on_load_topic_data
      });
    },
    on_load_topic_data: function (topic_data, edit, reletad_list) {
      console.log("[on_load_topic_data]topic_data:", topic_data, ' edit:', edit);
      this.topic_data = topic_data;
      this.set_title(this.video_title());
      this.forum_conf = this.get_forum(topic_data.fid);
      this.save_topic_data(topic_data);

      if (this.topic_data.video_info && !this.show_view_watch_limit) {
        var online_video = this.topic_data.video_info.online_videos[0];
        console.log('online_video:', online_video);
        var hash_id = online_video.hash_id;
        this.$store.state.user_visit.push(hash_id);
      }

      if (edit) {
        this.edit = edit;
      }

      if (reletad_list) {
        this.reletad_list = reletad_list;
      }

      console.log('reletad_list:', this.reletad_list);

      this.init();
    },
    get_side_threads: function () {
      try {
        if (this.$store.state.init.data.last_threads.datas.length > 0) {
          if (this.$store.state.init.data.last_threads.datas.length > 10) {
            return this.$store.state.init.data.last_threads.datas.slice(0, 10);
          }
          return this.$store.state.init.data.last_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    },
    on_switch_server: function (server_name) {
      this.select_server_name = server_name;
      localStorage.setItem('select_server_name', this.select_server_name);
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
        this.init();
        this.$nextTick(() => {

          var auto_play = () => {
            if (!this.player) {
              setTimeout(this.auto_play, 200);
            } else {
              this.player.play();
            }
          }
          auto_play();
        });
        if (this.$ga) {
          this.$ga.event({
            eventCategory: 'on_switch_server',
            eventAction: 'switch',
            eventLabel: this.select_server_name,
            eventValue: this.$store.state.hasLogin ? "1" : "2"
          });
        }
      });
    },
  },
  watch: {
    '$route'(to, from) {
      this.topic_data = undefined;
      this.$nextTick(this.load_video);
      console.log('watch $route change ');
    },
    select_server_name(newVal) {
      console.log('select_server_name change:', newVal);
      this.on_switch_server(newVal);
    }
  },
}
